import React, { ReactElement, useContext } from "react";
import { Link } from "react-router-dom";
import {
  Divider,
  Drawer,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  Toolbar,
} from "@mui/material";
import { AuthContext, Role } from "../security/AuthContext";
import { DashboardRounded, LockPerson } from "@mui/icons-material";

export function NavBar(): ReactElement {
  const { login } = useContext(AuthContext);
  const isAdmin = !!login?.data?.rol?.includes(Role.ADMIN);
  return (
    <Drawer open variant={"persistent"} anchor={"left"} style={{ zIndex: 0 }}>
      <Toolbar />
      <Divider />
      <List disablePadding>
        <ListItem key={"dashboard"}>
          <ListItemButton>
            <ListItemIcon>
              <DashboardRounded sx={{ marginRight: "5px" }} />
              <Link to={"dashboard"}>Dashboard</Link>
            </ListItemIcon>
          </ListItemButton>
        </ListItem>
        {isAdmin && (
          <ListItem key={"admin"}>
            <ListItemButton>
              <ListItemIcon>
                <LockPerson sx={{ marginRight: "5px" }} />

                <Link to={"admin"}>Admin</Link>
              </ListItemIcon>
            </ListItemButton>
          </ListItem>
        )}
      </List>
    </Drawer>
  );
}
