import { Address } from "../model/Common";
import { Page, Links } from "../model/FetchResponse";
import { useContext, useEffect, useState } from "react";
import { AuthContext } from "../security/AuthContext";
import { addAuthHeaders } from "../util/FetchUtils";

export interface Property {
  name: string;
  address: Address;
  _links?: Links;
}

export interface PropertyWrapper {
  properties: Property[];
}

interface PropertyListResponse {
  _embedded: PropertyWrapper;
  _links: {
    self: {
      href: string;
    };
    profile: {
      href: string;
    };
    search: {
      href: string;
    };
  };
  page: Page;
}

export interface PropertyQueryResponse {
  isLoading: boolean;
  response?: PropertyListResponse;
  error?: any;
}

export interface PropertyFetchResponse {
  isLoading: boolean;
  property?: Property;
  error?: any;
}

export function usePropertyFetch(
  url: string,
  id?: string,
): PropertyFetchResponse {
  const [isLoading, setIsLoading] = useState(false);
  const [property, setProperty] = useState<Property | undefined>(undefined);
  const [error, setError] = useState(undefined);

  const { login } = useContext(AuthContext);

  useEffect(() => {
    setIsLoading(true);

    const fetchData = async () => {
      if (!id) {
        return;
      }
      try {
        const response = await fetch(`${url}/${id}`, {
          headers: {
            ...addAuthHeaders(login),
          },
        });
        const responseBody = (await response.json()) as Property;
        setProperty(responseBody);
      } catch (e: any) {
        console.error(e);
        setError(e);
      } finally {
        setIsLoading(false);
      }
    };
    if (login?.accessToken) {
      fetchData().then(() => console.debug(`Property received`));
    }
  }, [url, id, login]);

  return { isLoading, property, error };
}

export function usePropertiesQuery(url: string): PropertyQueryResponse {
  const [isLoading, setIsLoading] = useState(false);
  const [response, setResponse] = useState<PropertyListResponse | undefined>(
    undefined,
  );
  const [error, setError] = useState(undefined);

  const { login } = useContext(AuthContext);

  useEffect(() => {
    setIsLoading(true);

    const fetchData = async () => {
      try {
        const response = await fetch(url, {
          headers: {
            ...addAuthHeaders(login),
          },
        });
        const responseBody = (await response.json()) as PropertyListResponse;
        setResponse(responseBody);
      } catch (e: any) {
        console.error(e);
        setError(e);
      } finally {
        setIsLoading(false);
      }
    };
    if (login?.accessToken) {
      fetchData().then(() => console.debug(`Property list received`));
    }
  }, [url, login]);

  return { isLoading, response, error };
}
