import React, { ReactElement, useContext, useEffect } from "react";
import {
  Button,
  Container,
  createTheme,
  CssBaseline,
  Divider,
  Paper,
  TextField,
  ThemeProvider,
  Typography,
} from "@mui/material";
import { useNavigate, useParams } from "react-router-dom";
import { Property, usePropertyFetch } from "../hooks/PropertyFetch";
import { Control, Controller, useForm } from "react-hook-form";
import { BASE_API_URL } from "../util/Constants";
import { TitleBar } from "./TitleBar";
import { addAuthHeaders } from "../util/FetchUtils";
import { AuthContext } from "../security/AuthContext";

const defaultTheme = createTheme();

interface FormInput {
  description: string;
  address: string;
  city: string;
  state: string;
  zip: string;
}

const defaultValues: FormInput = {
  description: "",
  address: "",
  city: "",
  state: "",
  zip: "",
};

function FormInputText(props: {
  name: keyof FormInput;
  control: Control<FormInput>;
  label: string;
}): ReactElement {
  const { name, control, label } = props;
  return (
    <Controller
      control={control}
      name={name}
      render={({
        field: { onChange, value },
        fieldState: { error },
        //formState,
      }) => (
        <TextField
          label={label}
          value={value}
          error={!!error}
          onChange={onChange}
        />
      )}
    />
  );
}

function propertyToForm(input?: Property): FormInput {
  if (input) {
    return {
      ...defaultValues,
      description: input.name,
      ...input.address,
    };
  }
  return defaultValues;
}

function formToProperty(input: FormInput): Property {
  const { address, city, state, zip } = input;
  return {
    name: input.description,
    address: { address, city, state, zip },
  };
}

export default function PropertyEditForm(props: { mode: "edit" | "create" }) {
  const { mode } = props;
  const { propertyId } = useParams();
  const { login } = useContext(AuthContext);
  const navigate = useNavigate();

  const { property, isLoading, error } = usePropertyFetch(
    `${BASE_API_URL}/properties`,
    propertyId,
  );
  const { control, handleSubmit, reset } = useForm<FormInput>({
    defaultValues: propertyToForm(property),
  });
  const address = property?.address;

  const onSubmit = handleSubmit(async (data) => {
    const body = JSON.stringify(formToProperty(data));
    try {
      if (mode === "edit") {
        const response = await fetch(
          `${BASE_API_URL}/properties/${propertyId}`,
          {
            method: "PUT",
            body: body,
            headers: {
              "Content-Type": "application/json",
              ...addAuthHeaders(login),
            },
          },
        );
        if (response.ok) {
          navigate(`/properties/${propertyId}`);
        } else {
          alert(response.statusText);
        }
      } else if (mode === "create") {
        const response = await fetch(`${BASE_API_URL}/properties`, {
          method: "POST",
          body: body,
          headers: {
            "Content-Type": "application/json",
            ...addAuthHeaders(login),
          },
        });
        if (response.ok) {
          const responseEntity = await response.json();
          console.info(JSON.stringify(responseEntity));
          navigate(`/properties/${responseEntity.id}`);
        } else {
          alert(response.statusText);
        }
      }
    } catch (e) {
      alert(e);
    }
  });

  useEffect(() => {
    if (property) {
      reset(propertyToForm(property));
    }
  }, [reset, property]);

  return (
    <ThemeProvider theme={defaultTheme}>
      <CssBaseline />
      <TitleBar title={"Property Edit"} />
      <Container>
        <Paper>
          <Typography variant="h3"> {property?.name}</Typography>
          {address && (
            <Typography>
              {address?.address} {address?.city}, {address?.state}{" "}
              {address?.zip}
            </Typography>
          )}
        </Paper>
      </Container>
      <Divider />
      <form onSubmit={onSubmit}>
        <Container>
          <Paper
            style={{ display: "grid", padding: "20px" }}
            sx={{
              "& .MuiTextField-root": { margin: 1 },
            }}
          >
            <FormInputText
              control={control}
              name={"description"}
              label={"Description"}
            />
            <FormInputText
              control={control}
              name={"address"}
              label={"Address"}
            />
            <FormInputText control={control} name={"city"} label={"City"} />
            <FormInputText control={control} name={"state"} label={"State"} />
            <FormInputText control={control} name={"zip"} label={"Zip"} />
            <Button
              size="small"
              type={"submit"}
              disabled={(isLoading || !!error) && mode === "edit"}
            >
              Submit
            </Button>
          </Paper>
        </Container>
      </form>
    </ThemeProvider>
  );
}
