import React, { ReactElement, useContext } from "react";
import { Button, Divider, Grid, Typography } from "@mui/material";
import { BASE_API_URL } from "../util/Constants";
import { Link } from "react-router-dom";
import { AuthContext } from "../security/AuthContext";
import { ArrowBack } from "@mui/icons-material";

const LOGIN_URL = `${BASE_API_URL}/oauth2/authorization/google?redirect_uri=${process.env.REACT_APP_URL}`;

export function Login(): ReactElement {
  const { logoutCallback, user } = useContext(AuthContext);

  const loggedIn = !!user;

  return (
    <>
      {loggedIn && (
        <Button component={Link} to={"/"}>
          <ArrowBack fontSize={"large"} />
        </Button>
      )}
      <Grid
        alignContent={"center"}
        justifyContent={"center"}
        alignItems={"center"}
        display={"flex"}
        height={"100vh"}
        flexDirection={"column"}
      >
        {!loggedIn && (
          <>
            <Typography>Not currently logged in</Typography>
            <Divider style={{ marginTop: "20px" }} />
            <Button component={Link} to={LOGIN_URL} variant={"outlined"}>
              <img src={"icons/google.svg"} alt="Google Login" />
              <Typography>Login with Google</Typography>
            </Button>
          </>
        )}
        {loggedIn && (
          <>
            <Typography>{`Logged in as ${user}`}</Typography>
            <Divider style={{ marginTop: "20px" }} />
            <Button onClick={logoutCallback}>Logout</Button>
          </>
        )}
      </Grid>
    </>
  );
}
