import {
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";
import React, { useContext, useEffect, useState } from "react";
import { AuthContext, Role } from "../../security/AuthContext";
import { BASE_API_URL } from "../../util/Constants";
import { addAuthHeaders } from "../../util/FetchUtils";

interface UserDetails {
  userName?: string;
  name?: number;
  roles?: Role[];
}

export function AdminUsersTab() {
  const { login } = useContext(AuthContext);
  const [rows, setRows] = useState<UserDetails[]>([]);

  useEffect(() => {
    fetch(`${BASE_API_URL}/users`, {
      headers: {
        ...addAuthHeaders(login),
      },
    }).then((response) => {
      if (response.ok) {
        response.json().then((json) => setRows(json?._embedded?.users ?? []));
      }
    });
  }, [login, setRows]);

  return (
    <TableContainer component={Paper}>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell style={{ fontWeight: "bold" }}>User Name</TableCell>
            <TableCell style={{ fontWeight: "bold" }}>Name</TableCell>
            <TableCell style={{ fontWeight: "bold" }}>Roles</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.userName}>
              <TableCell component={"th"} scope="row">
                {row.userName}
              </TableCell>
              <TableCell component={"th"} scope="row">
                {row.name}
              </TableCell>
              <TableCell component={"th"} scope="row">
                {row.roles?.join(", ")}
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
