import React, { useContext } from "react";
import { AuthContext, Role } from "../security/AuthContext";
import {
  AppBar,
  Avatar,
  Button,
  Divider,
  IconButton,
  Menu,
  MenuItem,
  Toolbar,
  Tooltip,
  Typography,
} from "@mui/material";
import { Link } from "react-router-dom";
import LogoutIcon from "@mui/icons-material/Logout";

export function TitleBar(props: { title: string }) {
  const { title } = props;
  const { logoutCallback, user, login } = useContext(AuthContext);
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <AppBar position="relative">
      <Toolbar style={{ justifyContent: "space-between" }}>
        <Typography>{title}</Typography>
        {!user && (
          <Button
            color={"primary"}
            variant={"contained"}
            component={Link}
            to={`/login`}
          >
            Login
          </Button>
        )}
        {user && (
          <div
            style={{
              display: "inline-flex",
              flexDirection: "column",
              alignItems: "center",
            }}
          >
            <Tooltip title="Account settings">
              <IconButton onClick={handleClick}>
                <Avatar alt={user} src={login?.data?.avatarUrl}>
                  {(login?.data?.name ?? user)
                    .split(" ")
                    .map((str: string) => str.at(0)?.toUpperCase())
                    .join("")}
                </Avatar>
              </IconButton>
            </Tooltip>
            <Menu
              anchorEl={anchorEl}
              open={open}
              onClose={handleClose}
              MenuListProps={{ sx: { margin: 1 } }}
            >
              {login?.data?.name && (
                <Typography align={"center"} style={{ fontWeight: "bold" }}>
                  {login.data.name}
                </Typography>
              )}
              <Typography align={"center"}>{user}</Typography>
              {login?.data?.rol?.includes(Role.ADMIN) && (
                <MenuItem
                  component={Link}
                  to={"/admin"}
                  color={"red"}
                  sx={{ justifyContent: "center" }}
                >
                  <Typography color={"red"} align={"center"}>
                    (ADMIN)
                  </Typography>
                </MenuItem>
              )}
              <Divider style={{ margin: 4 }} />
              <MenuItem
                onClick={() => {
                  handleClose();
                  logoutCallback();
                }}
              >
                <LogoutIcon style={{ marginRight: 15 }} />
                <Typography variant="inherit">Logout</Typography>
              </MenuItem>
            </Menu>
          </div>
        )}
      </Toolbar>
    </AppBar>
  );
}
