import {
  createContext,
  ReactElement,
  ReactNode,
  useCallback,
  useEffect,
  useState,
} from "react";

interface AuthContextState {
  loggedIn?: boolean;
  user?: string;
  loginCallback: (data: any) => void;
  logoutCallback: () => void;
  login?: LoginType;
}

export const AuthContext = createContext<AuthContextState>({
  loginCallback: () => {},
  logoutCallback: () => {},
});

const LOCAL_STORAGE_KEY = "user";

function readFromStorage() {
  const item = localStorage.getItem(LOCAL_STORAGE_KEY);
  if (item) {
    return JSON.parse(item);
  }
  return undefined;
}

function saveToStorage(login?: any) {
  if (login) {
    localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(login));
  } else {
    localStorage.removeItem(LOCAL_STORAGE_KEY);
  }
}

export enum Role {
  ADMIN = "ADMIN",
  USER = "USER",
}
interface LoginDataType {
  avatarUrl?: string;
  email?: string;
  exp?: number;
  name?: string;
  sub?: string;
  rol?: Role[];
}

export interface LoginType {
  accessToken?: string;
  data?: LoginDataType;
}

export function AuthContextProvider(props: {
  children: ReactNode;
}): ReactElement {
  const [loggedIn, setLoggedIn] = useState<boolean | undefined>(undefined);
  const [user, setUser] = useState<string | undefined>(undefined);

  const [login, setLogin] = useState<LoginType | undefined>(undefined);

  const logoutCallback = useCallback(() => {
    saveToStorage();
    setLoggedIn(false);
    setUser(undefined);
    setLogin(undefined);
  }, [setLoggedIn, setUser, setLogin]);

  const loginCallback = useCallback(
    (login: any) => {
      // if user has token expired, logout user
      if (Date.now() > login?.data?.exp * 1000) {
        logoutCallback();
      } else if (login?.data?.email) {
        saveToStorage(login);
        setLoggedIn(true);
        setUser(login.data.email);
        setLogin(login);
      }
    },
    [setLoggedIn, setUser, setLogin, logoutCallback],
  );

  useEffect(() => {
    const localLogin = readFromStorage();
    if (localLogin) {
      loginCallback(localLogin);
    } else {
      logoutCallback();
    }
  }, [loginCallback, logoutCallback]);

  return (
    <AuthContext.Provider
      value={{ login, loggedIn, user, loginCallback, logoutCallback }}
    >
      {props.children}
    </AuthContext.Provider>
  );
}
