import {
  AccordionDetails,
  AccordionSummary,
  Alert,
  AlertColor,
  Button,
  Snackbar,
  Typography,
} from "@mui/material";
import Accordion from "@mui/material/Accordion";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import TableChartIcon from "@mui/icons-material/TableChart";
import EditIcon from "@mui/icons-material/Edit";
import React, { useContext } from "react";
import { useSnackbar } from "../hooks/useSnackbar";
import { AuthContext, LoginType } from "../../security/AuthContext";
import { BASE_API_URL } from "../../util/Constants";
import { addAuthHeaders } from "../../util/FetchUtils";
import { AdminDataDetails } from "./AdminDataDetails";

function resetData(
  login: LoginType,
  showSnackbar: (text?: string, severity?: AlertColor) => void,
) {
  return async () => {
    if (!login?.accessToken) {
      showSnackbar("Not logged in!", "error");
      return;
    }
    try {
      const response = await fetch(`${BASE_API_URL}/admin/data/reset`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          ...addAuthHeaders(login),
        },
      });
      if (response.ok) {
        showSnackbar("Data successfully reset!", "success");
      } else {
        showSnackbar(response.statusText, "error");
      }
    } catch (e) {
      showSnackbar(e?.toString(), "error");
    }
  };
}

export function AdminDataTab() {
  const {
    snackbarOpen,
    showSnackbar,
    snackbarSeverity,
    snackbarText,
    onSnackbarClose,
  } = useSnackbar();

  const { login } = useContext(AuthContext);

  const resetDataHandler = resetData(login ?? {}, showSnackbar);

  return (
    <div>
      <Accordion>
        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
          <EditIcon style={{ marginRight: 5 }} />
          <Typography>Reset ALL data</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Button
            variant={"outlined"}
            style={{ marginLeft: 12 }}
            onClick={resetDataHandler}
          >
            Reset
          </Button>
        </AccordionDetails>
      </Accordion>

      <Accordion>
        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
          <TableChartIcon style={{ marginRight: 5 }} />
          <Typography>Show statistics</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <AdminDataDetails />
        </AccordionDetails>
      </Accordion>
      <Snackbar
        open={snackbarOpen}
        onClose={onSnackbarClose}
        autoHideDuration={5_000}
      >
        <Alert severity={snackbarSeverity}>{snackbarText}</Alert>
      </Snackbar>
    </div>
  );
}
