import {
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";
import React, { useContext, useEffect, useState } from "react";
import { BASE_API_URL } from "../../util/Constants";
import { addAuthHeaders } from "../../util/FetchUtils";
import { AuthContext } from "../../security/AuthContext";

interface TypeSummary {
  name: string;
  count: number;
}

export function AdminDataDetails() {
  const { login } = useContext(AuthContext);
  const [rows, setRows] = useState<TypeSummary[]>([]);

  useEffect(() => {
    fetch(`${BASE_API_URL}/admin/data/summary`, {
      headers: {
        ...addAuthHeaders(login),
      },
    }).then((response) => {
      if (response.ok) {
        response.json().then((json) => setRows(json));
      }
    });
  }, [login, setRows]);

  return (
    <TableContainer component={Paper}>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell style={{ fontWeight: "bold" }}>Entity type</TableCell>
            <TableCell style={{ fontWeight: "bold" }}>Count</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.name}>
              <TableCell component={"th"} scope="row">
                {row.name}
              </TableCell>
              <TableCell>{row.count}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
