import React from "react";
import "./App.css";
import Dashboard from "./components/Dashboard";
import { HashRouter, Navigate, Route, Routes } from "react-router-dom";
import PropertyView from "./components/PropertyView";
import PropertyEditForm from "./components/PropertyEditForm";
import { Login } from "./components/Login";
import { AuthContextProvider } from "./security/AuthContext";
import { OAuth2Redirect } from "./components/OAuth2Redirect";
import { Main } from "./components/Main";
import { AdminPage } from "./components/admin/AdminPage";
import AdminRoute from "./components/admin/AdminRoute";

function App() {
  return (
    <AuthContextProvider>
      <HashRouter>
        <Routes>
          <Route path="/" element={<Main />}>
            <Route index element={<Navigate to="/dashboard" replace />} />
            <Route path="dashboard" element={<Dashboard />} />
            <Route path="properties/:propertyId" element={<PropertyView />} />
            <Route
              path="properties/edit/:propertyId"
              element={<PropertyEditForm mode={"edit"} />}
            />
            <Route
              path="properties/create"
              element={<PropertyEditForm mode={"create"} />}
            />

            <Route
              path={"admin"}
              element={
                <AdminRoute>
                  <AdminPage />
                </AdminRoute>
              }
            />
          </Route>

          <Route path={"/login"} element={<Login />} />
          <Route path={"/oauth2/redirect"} element={<OAuth2Redirect />} />
        </Routes>
      </HashRouter>
    </AuthContextProvider>
  );
}

export default App;
